function App() {
  const handleCardClick = (user) => {
    const userData = `ID пользователя: ${user.id}\nИмя: ${user.firstName}\nEmail: ${user.email}\nПол: ${user.gender}\nМодель автомобиля: ${user.carModel}`;

    alert(userData);
  };

  return (
    <div className="App">
      <h1>User Cards App</h1>
      <div className="user-cards">
        {users.map((user) => (
          <div
            key={user.id}
            className="user-card"
            onClick={() => handleCardClick(user)}
          >
            <img src={user.image} alt={user.firstName} />
            <h3>{user.firstName}</h3>
          </div>
        ))}
      </div>
    </div>
  );
}

export default App;
